<template>
  <div class="loan-comforim">
    <div class="loan-info">
      <div class="loan-info-item flex flex-item active" @click="loanAmountClicked()">
        <span></span>
        <span class="flex-grow">{{loanInfo.loanAmount || 0}}</span>
        <i class="iconfont icon-117"></i>
      </div>
      <div class="loan-info-item flex flex-item active" @click="loanDayClicked()">
        <span></span>
        <span class="flex-grow">{{loanInfo.borrowTime || 0}}</span>
        <i class="iconfont icon-117"></i>
      </div>
      <div class="loan-info-item flex flex-item active" @click="loanPurposeClicked()">
        <span></span>
        <span class="flex-grow">{{loanInfo.loanPurpose}}</span>
        <i class="iconfont icon-117"></i>
      </div>
      <div class="loan-info-item flex flex-item active hidden">
        <span></span>
        <span class="flex-grow gray-text"></span>
        <i class="iconfont icon-117"></i>
      </div>
      <div class="loan-info-item item-bank flex flex-item flex-grow active" @click="choseBankCard()">
        <span></span>
        <span class="flex-grow bank-none" v-if="!bankCard.flag"></span>
        <span v-html="bankCard.bankName+'&nbsp;&nbsp;'+bankCard.bankAccount.substring(bankCard.bankAccount.length-4,bankCard.bankAccount.length)" class="flex-grow" v-if="bankCard.flag"></span>
        <i class="icon iconfont icon-117"></i>
      </div>
      <div class="loan-info-item flex flex-item active" @click="chooseVip()">
        <span></span>
        <span class="flex-grow">{{loanInfo.expireTime ? '' + loanInfo.expireTime : (vipData.type ? '' : '')}}<span class="selected-vip">{{vipData.type ? '' + vipData.typeDescribe : ''}}</span></span>
        <i class="iconfont icon-117"></i>
      </div>
      <div class="loan-info-item flex flex-item">
        <span></span>
        <span class="flex-grow"><input class="identify-code-input"  v-model="loanInfo.msgCode" type="tel" placeholder="" maxlength="6"></span>
        <div v-if="!isSend" class="identify-code-btn" @click="sendValidateCode()"></div>
        <div v-if="isSend" class="identify-code-btn">{{delayTime}}s</div>
      </div>
    </div>
    <div class="loan-info">
      <div class="loan-info-item item-middle flex flex-item active">
        <span></span>
        <span>{{loanInfo.interest || 0}}</span>
      </div>
      <div class="loan-info-item item-middle flex flex-item">
        <span></span>
        <span>{{loanInfo.repayTotalAmount || 0}}</span>
        <a class="flex flex-item flex-grow" @click="seeDetail()">
          
          <i class="icon iconfont icon-117"></i>
        </a>
      </div>
    </div>
    <p class="comfirm-protocol flex flex-item flex-justify">
      <span @click="agreeProtocols" class="text-gary">
        <i :class="{'icon-not-chose': isChosed}" class="iconfont icon-correct-marked"></i>
        <span @click.stop="checkServicesProtocols"></span>
      </span>
    </p>
    <p class="forbid-borrow-stu"></p>
    <a class="button button-primary" @click="submit"></a>
    <alert-item v-if="checkDetailFlag" :loanDetail="loanInfo" v-on:listenChildEvent="closeAlert"></alert-item>
  </div>
</template>
<script type="text/ecmascript-6">
  import AlertItem from 'base/alertItem/alert-item'
  import {doPost, popup, navigate, eeLogUBT, toast, log} from 'common/js/drivers'
  import * as types from 'config/api-type'
  import {pageIdentity, loanPurposeStatus} from 'common/js/constants'
  import Picker from 'better-picker'

  export default {
    data() {
      return {
        loanInfo: {
          loanAmount: '',
          borrowTime: '',
          interest: '',
          syntheticalFee: '',
          realLoanAmount: '',
          repayTotalAmount: '',
          annualizedRate: '',
          bankList: [],
          loanPurpose: '',
          memberExpireDay: '',
          expireTime: '',
          mobile: '',
          productCode: '',
          msgCode: ''
        },
        bankCard: {
          bankName: '',
          bankAccount: '',
          flag: false
        },
        amountList: [],
        dayList: [],
        isChosed: false,
        checkDetailFlag: false,
        params: {},
        isSend: false,
        delayTime: 0,
        vipData: {}
      }
    },
    created: function() {
      let {productCode, mobile} = this.$route.query
      this.params = {productCode, mobile}
      this.loanInfo.productCode = productCode
      this.loanInfo.mobile = mobile
    },
    mounted() {
      this.init()
      this._initLoanPurpose()
      eeLogUBT('LoanPage.Load.Goin', 'goin')
    },
    methods: {
      init: function() {
        let {bankName, bankAccount} = this.$route.query
        if (bankName || bankAccount) { // 
          this.bankCard = {
            bankName: bankName,
            bankAccount: bankAccount,
            flag: true
          }
        }
        doPost(types.BORROW, this.params, {
          success: (oData) => {
            let {dayList, amountList, interestRate, memberExpireDay, expireTime} = oData.data
            this.dayList = dayList
            this.amountList = amountList
            this._initDayPicker()
            this._initAmountPicker()
            this.loanInfo.borrowTime = dayList[0].value
            this.loanInfo.loanAmount = amountList[0].value
            this.loanInfo.interestRate = interestRate // 
            this.loanInfo.memberExpireDay = memberExpireDay // 
            this.loanInfo.expireTime = expireTime // 
            this._resetData()
            // this.loanInfo = oData.data
            if (oData.data.bankList.length !== 0 && !bankName && !bankAccount) { // 
              let payCard = oData.data.bankList[0]
              this.bankCard = payCard
              this.bankCard.flag = true
            }
          },
          error: (oData) => {
            popup(null, null, oData.msg || '')
          }
        })
      },
      sendValidateCode() {
        let {mobile} = this.loanInfo
        if (!/^1\d{10}$/.test(mobile)) {
          popup('', null, '')
          return
        }
        doPost(types.SMSCODE, {
          mobile: mobile,
          smsType: '1'
        }, {
          success: (oData) => {
            toast('')
            this.isSend = true
            this.delayTime = 59
            let timer = setInterval(() => {
              if (this.delayTime === 1) {
                this.isSend = false
                this.delayTime = 0
                clearInterval(timer)
              }
              this.delayTime--
            }, 1000)
          },
          error: (oData) => {
            popup(null, null, oData.msg || '')
          }
        })
      },
      agreeProtocols: function() {
        this.isChosed = !this.isChosed
      },
      submit: function() {
        let self = this
        if (this.isChosed) {
          popup(null, null, '')
          return
        }
        if (!this.loanInfo.loanPurpose) {
          popup(null, null, '')
          return
        }
        if (!self.bankCard.bankAccount) {
          popup(null, null, '')
          return
        }
        if (!this.loanInfo.memberExpireDay) {
          if (!this.vipData.type) {
            popup(null, null, '')
            return
          }
          if (Number(this.vipData.effectTime) < Number(this.loanInfo.borrowTime)) {
            popup(null, null, '')
            return
          }
        } else {
          if (!this.vipData.type) {
            if (Number(this.loanInfo.memberExpireDay) < Number(this.loanInfo.borrowTime)) {
              popup(null, null, '')
              return
            }
          } else {
            if ((Number(this.loanInfo.memberExpireDay) + Number(this.vipData.effectTime)) < Number(this.loanInfo.borrowTime)) {
              popup(null, null, '')
              return
            }
          }
        }

        if (!this.loanInfo.msgCode) {
          popup(null, null, '')
          return
        }

        let {borrowTime, interest, loanPurpose, mobile, productCode, msgCode, loanAmount, annualizedRate, realLoanAmount, repayTotalAmount} = this.loanInfo
        let {bankName, bankAccount} = this.bankCard
        let {type, realFee} = this.vipData
        eeLogUBT('LoanPage.Action.Submit', 'click')
        let param = {borrowTime, interest, loanPurpose, mobile, productCode, msgCode, loanAmount, annualizedRate, realLoanAmount, repayTotalAmount, bankName, bankAccount, type, memberFee: realFee}
        doPost(types.BORROW_CONFIRM, param, {
          success: (oData) => {
            let param = `orderNo=${oData.data.orderNo}`
            navigate('LOAN_RESULT', '', {url: pageIdentity.LOAN_RESULT, param}, null, 'ROOT')
          },
          error: (oData) => {
            popup(null, null, oData.msg || '')
          }
        })
      },
      choseBankCard: function() {
        let self = this
        eeLogUBT('LoanPage.Action.BankCard', 'click')
        navigate('CHOOSE_BANK', '', {url: pageIdentity.CHOOSE_BANK}, {
          success: function(oData) {
            if (oData.status === '0') {
              let {bankName, bankAccount} = oData.data
              self.bankCard = {
                bankName: bankName,
                bankAccount: bankAccount,
                flag: true
              }
            }
          },
          error: function(oData) {
            popup(null, null, oData.msg || '')
          }
        })
      },
      seeDetail: function() {
        eeLogUBT('LoanPage.Action.Alert.Detail', 'click')
        setTimeout(() => {
          this.checkDetailFlag = true
        }, 100)
      },
      closeAlert: function(isOpen) {
        this.checkDetailFlag = isOpen
      },
      checkServicesProtocols: function() {
        eeLogUBT('LoanPage.Action.Agreement', 'click')
        navigate('SERVICES_PROTOCOLS', '', {url: pageIdentity.SERVICES_PROTOCOLS})
      },
      loanPurposeClicked() {
        this.loanPurposePicker.show()
      },
      loanDayClicked() {
        this.dayPicker && this.dayPicker.show()
      },
      loanAmountClicked() {
        this.amountPicker && this.amountPicker.show()
      },
      chooseVip() {
        let self = this
        navigate('VIP', '', {url: pageIdentity.VIP}, {
          success(oData) {
            log('', JSON.stringify(oData))
            let {type, discountFee, effectTime, typeDescribe} = oData.data
            console.log(type, discountFee, effectTime, typeDescribe)
            self.vipData = oData.data
          },
          error(oData) {
            popup(null, null, oData.msg || '')
          }
        })
      },
      _initLoanPurpose() {
        this.loanPurposePicker = new Picker({
          'data': [loanPurposeStatus],
          'selectedIndex': [0]
        })
        this.loanPurposePicker.on('picker.select', (val, index) => {
          this.loanInfo.loanPurpose = val[0]
        })
      },
      _initDayPicker() {
        let self = this
        this.dayPicker = new Picker({
          'data': [self.dayList],
          'selectedIndex': [0]
        })
        this.dayPicker.on('picker.select', (val, index) => {
          this.loanInfo.borrowTime = val[0]
          this._resetData()
        })
      },
      _initAmountPicker() {
        let self = this
        this.amountPicker = new Picker({
          'data': [self.amountList],
          'selectedIndex': [0]
        })
        this.amountPicker.on('picker.select', (val, index) => {
          this.loanInfo.loanAmount = val[0]
          this._resetData()
        })
      },
      _resetData() {
        let {loanAmount, borrowTime, interestRate} = this.loanInfo
        this.loanInfo.interest = (borrowTime * loanAmount * interestRate) / 100
        this.loanInfo.annualizedRate = `${interestRate * 360}`
        this.loanInfo.repayTotalAmount = Number(loanAmount) + Number(this.loanInfo.interest)
      }
    },
    components: {
      AlertItem
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/base"
  .loan-comforim
    margin-bottom: .3rem

  .loan-info
    background-color: #fff
    margin-top: .1rem

  .loan-info-item
    height: 1rem
    font-size: .28rem
    padding-left: .4rem
    padding-right: .4rem
    span
      color: #525252
      &:last-of-type
        font-size: .28rem
        margin-left: .24rem
    .identify-code-btn
      color: #0079ff !important
      text-align: center
    .identify-code-input
      width: 100%
      outline: none
    .gray-text
      font-size: .2rem!important
      color: #939393!important

  .item-middle
    height: .72rem
    position: relative
    span
      &:last-of-type
        color: #525252
        font-size: .28rem
    a
      position: absolute
      right: .4rem
      height: .72rem
      color: #0079ff !important
      top: 0
      i
        color: #0079ff
        --font-size: .25rem
        margin-top: -.03rem


  .item-bank
    span
      &:last-of-type
        color: #525252
        font-size: .28rem
        margin-right: .1rem

  .comfirm-protocol
    font-size: .2rem
    margin-top: .2rem
    i
      color: green
      font-size: .22rem
      margin-top: -0.04rem
      margin-right: 0.05rem
    span
      span
        color: #008aff

  .button
    width:90%
    margin: 0 auto
    margin-top: .2rem

  .forbid-borrow-stu
    color: #9d9d9d
    font-size: .2rem
    margin-top: .05rem
    text-align: center

  .icon-not-chose
    color: lightgrey !important

  .icon-back
    width: .12rem
    height: .2rem
    margin-right: .4rem

  .bank-none
    color: #008aff !important

  .phone-item
    font-size: .28rem !important
    color: #525252 !important

  .input-validate
    input
      width: 2.4rem
      border-radius: .06rem
      height: .76rem
      background-color: #eeeff3
      padding-left: .2rem
      outline: none
    div
      margin-right: .35rem
      width: 1.76rem
      background-color: #89cd40
      height: .76rem
      border-radius: .06rem
      text-align: center
      line-height .76rem
      margin-left: .1rem
      color: #fff

  .selected-vip
    color: #9d9d9d!important

  input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    color: #aeaeae
  }
  input:-moz-placeholder, textarea:-moz-placeholder {
    color: #aeaeae
  }
  input::-moz-placeholder, textarea::-moz-placeholder {
    color: #aeaeae
  }
  input:-ms-input-placeholder, textarea:-ms-input-placeholder {
    color: #aeaeae
  }

</style>



